<template>
  <div ref="chart" style="width: 80%; height: 376px"></div>
</template>

<script>
export default {
  data() {
    return {};
  },
  mounted() {
    this.getEchartData();
  },
  methods: {
    getEchartData() {
      var myChart = this.$echarts.init(this.$refs.chart, "light"); //第二个参数是主题色 有dark和light，自定义可以去官网下和在线编辑
      myChart.showLoading();
      myChart.setOption({
        dataZoom: [
          {
            type: "slider", //也是缩放 但和toolbox的不同
            // type:'inside'  //不直观 双击或者滚轮操作
            xAxisIndex: 0,
          },
          {
            type: "slider", //一个默认是x轴，两个都加需要配置这个yAxisIndex
            yAxisIndex: 0,
            start: 0,
            end: 80, //指定初始的缩放情况
          },
        ],
        title: {
          text: "ECharts 示例",
          link: "https://www.baidu.com",
          textStyle: {
            color: "red",
          },
        },
        tooltip: {
          //提示线
          trigger: "axis",
          // triggerOn:"click" //触发时机
        },
        toolbox: {
          //提供的五个工具栏
          feature: {
            saveAsImage: {}, //导出图片
            dataView: {}, //数据视图
            restore: {}, //重置
            dataZoom: {}, //区域缩放
            magicType: {
              //类型转换
              // type: ["bar", "line"],
            },
          },
        },
        legend: {},
        // grid: {
        //   show: true,
        //   //位置
        //   left: "3%",
        //   width: 1000,
        //   containLabel: true,
        // },
        xAxis: {
          type: "category",
          boundaryGap: false, //紧贴左侧轴线
          data: ["衬衫", "羊毛衫", "雪纺衫", "裤子", "高跟鞋", "袜子"],
        },
        yAxis: {
          type: "value",
          // sacle:true //缩放 ，脱离0值（即数值很近视觉效果能明显）
        },
        series: [
          {
            name: "销量",
            type: "line",
            smooth: true, //折线进阶曲线
            stack: "Total", //堆叠图 线不交叉
            data: [5, 30, 36, 52, 10, 20],
            // areaStyle: { color: "#000" }, //面积图 为空默认蓝色 但不能没有
          },
          {
            name: "效率",
            type: "line",
            smooth: true, //折线进阶曲线
            stack: "Total",
            data: [5, 20, 36, 8, 10, 90],
            // areaStyle: { color: "#000" }, //面积图 为空默认蓝色 但不能没有
          },
          {
            name: "价格",
            type: "line",
            smooth: true, //折线进阶曲线
            stack: "Total",
            data: [5, 20, 26, 10, 10, 0],
            // areaStyle: { color: "#000" }, //面积图 为空默认蓝色 但不能没有
          },
        ],
      });
      myChart.hideLoading();
      window.onresize = function () {
        myChart.resize();
      };
      // this.$on("hook:destroyed", () => {
      //   window.removeEventListener("resize", function () {
      //     myChart.resize();
      //   });
      // });
    },
  },
};
</script>

